"use client";
import React from "react";

interface ExamModalProps {
  isOpen: boolean;
  onClose: () => void;
  moduleId: number;
}

const ExamModal: React.FC<ExamModalProps> = ({ isOpen, onClose, moduleId }) => {
  if (!isOpen) return null;

  return (
    <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center">
      <div className="bg-white p-6 rounded-lg shadow-lg w-96">
        <h2 className="text-xl font-bold">Iniciar Examen</h2>
        <p className="mt-2">¬øEst√°s listo para comenzar?</p>
        <div className="flex justify-end gap-4 mt-4">
          <button onClick={onClose} className="px-4 py-2 border rounded">
            Cancelar
          </button>
          <a href={`/exams/${moduleId}`} className="px-4 py-2 bg-blue-500 text-white rounded">
            Comenzar
          </a>
        </div>
      </div>
    </div>
  );
};

export default ExamModal;
